import axios from 'axios'
import $ from 'jquery'
import navbar from './navbar'
export default {
    name: 'sortdetail',
    data() {
        return {
            lists: [],
            isshow: false,
            filter: false,
            arealist: '',
            has: false,
            totalurt: '',
            isshai: false,
        }
    },
    methods: {
        change: function (ev) {
            var current = $(ev.currentTarget).attr('class')
            ev.currentTarget.className = current == 'bgred' ? '' : 'bgred'
        },
        fc: function () {
            window.location.href = '/#/search'
        },
        shaixuan: function () {
            var arr = []
            var urlfrom = ""
            var urlto = ""
            var urlarea = ""
            var urltype = ''
            var urlownshop = ''
            var fromvalue = $('#price_from').val()
            var tovalue = $('#price_to').val()
            var selet = $('#area_id').val()
            var currentbg = $('.active .bgred')
            var ownshop = $('#own_shop').attr('class')
            console.log(selet)
            if (ownshop) {
                urlownshop = '&own_shop=1'
            } else {
                urlownshop = ''
            }
            if (fromvalue) {
                urlfrom = '&price_from=' + fromvalue
            } else {
                urlfrom = ''
            }
            if (tovalue) {
                urlto = '&price_to=' + tovalue
            } else {
                urlto = ''
            }
            for (var i = 0; i < currentbg.length; i++) {
                // console.log($(currentbg[i]).attr('id'))
                arr.push('&' + $(currentbg[i]).attr('id') + '=1')
            }
            for (var n = 0; n < arr.length; n++) {
                urltype += arr[n]
            }
            this.totalurl = 'http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&keyword=&area_id=' + selet + urlfrom + urlto + urlownshop + urltype + '&page=10&curpage=1'
            console.log(this.totalurl)
            axios.get(this.totalurl)
                .then(function (res) {
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this))
                .catch(function (err) {
                    console.log(err)
                })
            this.filter = false
            this.isshai = true

        },
        zong: function (ev) {
            this.isshow = !this.isshow
            $(ev.currentTarget).attr('class', 'reda')
            $(ev.currentTarget).siblings('.reda').attr('class', '')
        },
        filte: function (ev) {

            $(ev.currentTarget).attr('class', 'reda')
            $(ev.currentTarget).siblings('.reda').attr('class', '')
            this.filter = true
        },
        xiaoliang: function (ev) {
            $(ev.currentTarget).attr('class', 'reda')
            $(ev.currentTarget).siblings('.reda').attr('class', '')
            this.isshow = false
            var urlxiaoliang = ''
            if (!this.isshai) {
                urlxiaoliang = 'http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=' + this.$route.params.id + '&page=10&curpage=1&b_id=' + this.$route.params.id + '&key=1&order=2'
            } else {
                urlxiaoliang = this.totalurl + '&key=1&order=2'
            }
            axios.get(urlxiaoliang)
                .then(function (res) {
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this))
                .catch(function (err) {
                    console.log(err)
                })
        },
        click1: function (ev) {
            console.log(ev.currentTarget.children)
            $(ev.currentTarget).siblings().attr('class', '')
            $(ev.currentTarget).attr('class', 'reda')
            axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=365&page=10&curpage=1&b_id=' + this.$route.params.id)
                .then(function (res) {
                    console.log(res.data)
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
            this.isshow = false
        },
        click2: function (ev) {
            $(ev.currentTarget).siblings().attr('class', '')
            $(ev.currentTarget).attr('class', 'reda')
            axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=' + this.$route.params.id + '&page=10&curpage=1&b_id=' + this.$route.params.id + '&key=3&order=2')
                .then(function (res) {
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
            this.isshow = false
        },
        click3: function (ev) {
            $(ev.currentTarget).siblings().attr('class', '')
            $(ev.currentTarget).attr('class', 'reda')
            axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=' + this.$route.params.id + '&page=10&curpage=1&b_id=' + this.$route.params.id + '&key=3&order=1')
                .then(function (res) {
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
            this.isshow = false
        },
        click4: function (ev) {
            $(ev.currentTarget).siblings().attr('class', '')
            $(ev.currentTarget).attr('class', 'reda')
            axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=' + this.$route.params.id + '&page=10&curpage=1&b_id=' + this.$route.params.id + '&key=2&order=2')
                .then(function (res) {
                    this.lists = res.data.datas.goods_list
                    if (this.lists.length == 0) {
                        this.has = true
                    } else {
                        this.has = false
                    }
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
            this.isshow = false
        }
    },
    components: {
        navbar,
    },
    mounted: function () {
        console.log()
        axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_list&b_id=365&page=10&curpage=1&b_id=' + this.$route.params.id)
            .then(function (res) {
                this.lists = res.data.datas.goods_list
                if (this.lists.length == 0) {
                    this.has = true
                } else {
                    this.has = false
                }
            }.bind(this)).catch(function (err) {
                console.log(err)
            })
        // 
        axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=index&op=search_adv')
            .then(function (res) {
                this.arealist = res.data.datas.area_list
                if (this.lists.length == 0) {
                    this.has = true
                } else {
                    this.has = false
                }
            }.bind(this)).catch(function (err) {
                console.log(err)
            })
    },
}




