import bus from './bus.js'
export default {
    name: 'menu',
    data() {
        return {
            isshow: false
        }
    },
    created: function () {
        bus.$on('sort',function(){
           this.isshow=!this.isshow
        }.bind(this))
        bus.$on('shop',function(){
           this.isshow=!this.isshow
        }.bind(this))
    },
    methods:{
        fade:function(){
            this.isshow=false
        }
    }
}




