
import $ from 'jquery'
import shop from './gouwuche'
import denavbar from './detailnavbar'
import city from './city'
import axios from 'axios'
import bus from './bus.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
    name: 'productdetail',
    components: {
        Swiper,
        SwiperItem,
        denavbar,
        shop,
        city,
        key: '',


    },
    methods: {
        getCookie: function (c_name) {
            var c_start, c_end
            if (document.cookie.length > 0) {
                c_start = document.cookie.indexOf(c_name + "=")
                if (c_start != -1) {
                    c_start = c_start + c_name.length + 1
                    c_end = document.cookie.indexOf(";", c_start)
                    if (c_end == -1) c_end = document.cookie.length
                    return unescape(document.cookie.substring(c_start, c_end))
                }
            }
            return ""
        },
        addtoshop: function (a) {
            console.log(this.number)
            console.log(a)
            var qs = require('qs');
            axios.post('http://www.tianxiajiancai.com.cn/mobile/index.php?act=member_cart&op=cart_add',
                qs.stringify({ key: this.key, goods_id: a, quantity: this.number }),
                {
                    headers: {
                        'Content-Type': "application/x-www-form-urlencoded",
                    },
                }).then(function (res) {
                    console.log(res)
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
        },
        add: function (ev) {
            this.number++
        },
        red: function () {

            if (this.number <= 1) {
                return
            }
            this.number--
        },
        shopadd: function () {
            this.size = true
            console.log('添加事件')
        },
        addshop: function () {
            this.size = true
        },
        fade: function () {
            this.size = false

        },
        sizes: function () {
            this.size = true
            console.log('chuxian')
        },
        dianji: function () {
            this.iscity = true
            bus.$emit('id', this.$route.params.id)
        },
        load: function () {
            axios.get('http://www.tianxiajiancai.com.cn/mobile/index.php?act=goods&op=goods_detail&goods_id=' + this.$route.params.id + '&key=')
                .then(function (res) {
                    this.productdetail = res.data.datas
                    this.commendlist = res.data.datas.goods_commend_list
                    // var list=this.productdetail.goods_image.split(',')
                    this.pic = this.productdetail.goods_image.split(',')
                    for (var i = 0; i < this.pic.length; i++) {
                        if (!this.list[i].img) {
                            this.list.push({ img: '' })
                        }
                        this.list[i].img = this.pic[i]
                    }
                    this.list.pop()
                    this.istrue = true
                }.bind(this)).catch(function (err) {
                    console.log(err)
                })
        }
    },
    data() {
        return {
            productdetail: [],
            commendlist: [],
            list: [{ img: '', hh: '12' }],
            pic: [],
            istrue: false,
            iscity: false,
            pro: '',
            city: '',
            qu: '',
            fee: '',
            size: false,
            number: 1,
        }
    },
    created: function () {
        this.key = this.getCookie('key')
        bus.$on('zuihou', function () {
            this.iscity = false
        }.bind(this))
        bus.$on('fa', function () {
            this.iscity = false
        }.bind(this))
        bus.$on('pro', function (data) {
            this.pro = data.city
        }.bind(this))
        bus.$on('city', function (data) {
            this.city = data.city
        }.bind(this))

        bus.$on('qu', function (data) {
            this.qu = data.city
        }.bind(this))

        bus.$on('fee', function (data) {
            this.fee = data
        }.bind(this))

    },
    mounted: function () {
        console.log('hahah')
        window.onscroll = aa
        function aa() {
            if (document.body.scrollTop > 100) {
                var nav = $('#denav')
                nav.css('visibility', 'visible')
            } else if (document.body.scrollTop < 100) {
                var nav = $('#denav')
                nav.css('visibility', 'hidden')
            }
        }
        aa()
        this.load()
    },
    watch: {
        '$route': function () {
            this.$router.go(0)
        }
    }

}




